*{
    box-sizing: border-box;
}
.block {
    height: 80px;
    width: 80px;
    display: inline-block;
}

.row {
    display: flex;
}

.piece {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.highlight {
    border: 3px solid gold;
}

.attack {
    border: 3px solid red;
}